<template>
    <el-form ref="hProperty" :model="hProperty" :rules="houseFormRules" label-width="100px" style="margin:20px ;width:60%; min-width:600px;" >
        <el-form-item label="" prop="title">
            <el-input v-model="hProperty.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
            <el-input v-model="hProperty.name"></el-input>
        </el-form-item>

        <el-form-item label="" prop="rentPrice">
            <el-input v-model="hProperty.rentPrice"></el-input>
        </el-form-item>
         <el-form-item label="" prop="rentMethod">
            <el-input v-model="hProperty.rentMethod"></el-input>
        </el-form-item>
        <el-form-item label="" prop="houseOrientation">
            <el-input v-model="hProperty.houseOrientation"></el-input>
        </el-form-item>
         <el-form-item label="" prop="floor">
            <el-input v-model="hProperty.floor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="houseType">
            <el-input v-model="hProperty.houseType"></el-input>
        </el-form-item>
        <el-form-item label="" prop="decorate">
            <el-input v-model="hProperty.decorate"></el-input>
        </el-form-item>

        <el-form-item label="" prop="area">
            <el-input v-model="hProperty.area"></el-input>
        </el-form-item>

        <el-form-item label="" prop="addr">
            <el-input v-model="hProperty.addr"></el-input>
        </el-form-item>

        <el-form-item label="" prop="phone">
            <el-input v-model="hProperty.phone"></el-input>
        </el-form-item>

        <el-form-item label="">
            <el-select v-model="hProperty.status" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="">
            <el-radio-group v-model="hProperty.isPublic">
                <el-radio :label="1" ></el-radio>
                <el-radio :label="0" ></el-radio>
            </el-radio-group>
        </el-form-item>

        <el-form-item label="">
        <div class="upload-block">
            <div class="source">
                <el-upload
                  action="http://up-z1.qiniu.com"
                  list-type="picture-card"
                  :on-preview="handlePictureCardPreview"
                  :on-remove="handleRemove"
                  :on-success="uploadSuccess"
                  :data="postData">
                  <i class="el-icon-plus"></i>
            </el-upload>
            </div>
            <el-dialog v-model="dialogVisible" size="tiny">
                <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </div>

        </el-form-item>
        <el-form-item label="">
            <el-input type="textarea" v-model="hProperty.desc"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click.native="onSubmit" ></el-button>
            <el-button @click.native.prevent></el-button>
        </el-form-item>
    </el-form>
</template>
<style lang="scss" scoped>
    .upload-block {
        border: 1px solid #eaeefb;
        border-radius: 4px;
        padding: 24px;
    }

</style>
<script>
import {savehouse, getQiniuToken, getDomain} from '../../api/api';
import constants from '../../common/js/constants'

    export default {
        data() {
            return {
                hProperty: {
                    title:'',
                    name: '',
                    rentPrice:'',
                    rentMethod:'',
                    houseOrientation:'',
                    houseType:'',
                    floor:'',
                    decorate:'',
                    area:'',
                    addr: '',
                    phone: '',
                    status: '',
                    desc: '',
                    isPublic: 1
                },
                dialogImageUrl: '',
                dialogVisible: false,
                postData : {
                    token: this.qiniuToken
                },
                qiniuDomain: "",
                qiniuToken: "",
                houseFormRules: {
                    title: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    name: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    rentPrice: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    rentMethod: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    houseOrientation: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    houseType: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    floor: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    area: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    decorate: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    addr: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    phone: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                images:[]
            }
        },
        methods: {
            addImages(str) {
                this.images.push(str);
            },
            delImages(str) {
                var tmp = this.images;
                this.images.forEach((item, index) => {
                    if (item == str) {
                        tmp.splice(index,1);
                    }
                });
                this.images = tmp;
            },
            onSubmit() {
                this.$refs.hProperty.validate((valid) => {
                    if (valid) {
                        let user = JSON.parse(sessionStorage.getItem('user'))
                            this.hProperty.type = 0;
                            this.hProperty.images = this.images;
                            savehouse(Object.assign({}, this.hProperty)).then((res) => {
                                if (res.data.status == 200 ) {
                                    this.$router.push({ path: '/admin/rentList' });
                                    this.$message({
                                         message: '',
                                         type: 'success'
                                    });
                                }
                                    //NProgress.done();
                            });
                    }
                });
            },
            uploadSuccess(res, file, fileList) {
                this.dialogImageUrl = this.qiniuDomain + '/' + res.key;
                this.addImages(this.dialogImageUrl);
            },
            handleRemove(file, fileList) {
                if (file.response == undefined) {
                    this.delImages(file.url);
                } else {
                    this.delImages(this.qiniuDomain + '/' + file.response.key);
                }

            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            init() {
                getDomain({}).then((res) => {
                    this.qiniuDomain = res.data.domain
                });
                getQiniuToken({}).then((res) => {
                    this.qiniuToken = res.data.token
                    this.postData.token = this.qiniuToken;
                });
            }
        },
        mounted() {
            this.init();
        },
    }

</script>


